}

.nav__item:has(.nav__link--current) {
  background-color: #4d1299;
}

.nav__item:not(:has(.nav__link--current)):hover {
  background-color: #4d1299;
}

.nav__item:not(:has(.nav__link--current)):hover .nav__link {
  color: #fff;
}

.nav__item--light:has(.nav__link--current) {
