.backdrop {
  position: fixed;
  top: 0;
  left: 0;

  z-index: 999999;
  width: 100%;
  height: 100%;
  overflow-y: scroll;
  background-color: $overlay-color;
  opacity: 1;
  transition: opacity $cubic, visibility $cubic;
  @media screen and (min-width: 768px) {
    padding-top: 50px;
  }
  
}

.backdrop--support {
    display: flex;
    align-items: center;
    justify-content: center;
}

.backdrop--hidden {
  visibility: hidden;
  opacity: 0;
  pointer-events: none;
  transition: opacity $cubic, visibility $cubic;
}

.modal {
    position: relative;
    display: flex;
    flex-direction: column;
    padding: 30px;
      border-radius: 30px;
        background: $primary-white-color;
        transform: scale(1);
        transition: transform $cubic;
        width: 335px;
      
        .backdrop--hidden & {
          transform: scale(0.8);
        }
      
        @media screen and (min-width: 768px) {
          transform: scale(1);
          width: 480px;
          // max-height: 100vh;
      
          .backdrop--hidden & {
            transform: scale(0.8);
          }
}



  @media screen and (min-width: 1440px) {
    min-height: 581px;
    gap: 24px;
  }
}

.modal--support {
  gap: 24px;
  padding-top: 40px;

  @media screen and (min-width: 768px) {
    padding: 60px;
  }

}

.modal__title {
   font-weight: 600;
    font-size: 20px;
    line-height: 1.15;
    max-width: 250px;
  
    @media screen and (min-width: 768px) {
      max-width: 300px;
      
    }
  
    @media screen and (min-width: 1440px) {
      font-size: 22px;
      line-height: 1.23;
    }
}


.socials--modal {
  display: flex;
  flex-direction: column;
  gap: 24px;
}

.socials__item--modal {
   display: flex;
   align-items: center;
   gap: 16px;
}

.socials__text {
  color: $primary-black-color;
  font-weight: 600;
  font-size: 16px;
  line-height: 1.38;
  transition: color $cubic;
  @media screen and (min-width: 1440px) {
      font-size: 18px;
      line-height: 1.39;
  }
&:hover,
&:focus {
  color: $main-accent-color;
}
}

.modal__tel {
  display: block;
      font-family: 'Open Sans', sans-serif;
      font-weight: 700;
    margin: 0;
}

.close-modal-btn {
    position: absolute;
    z-index: 20;
    width: 22px;
    height: 22px;
    top: 18px;
    right: 18px;
  
    @media screen and (min-width: 1440px) {
      width: 28px;
      height: 28px;
      top: 24px;
      right: 24px;
    }

  margin-left: auto;
  background-color: transparent;
  border: none;
  outline: none;
  color: $primary-black-color;
  cursor: pointer;

  .close-modal-icon {
    transition: stroke $cubic;
    stroke: currentColor;
    width: 22px;
    height: 22px;

    @media screen and (min-width: 1440px) {
      width: 28px;
      height: 28px;
    }
  }

  &:hover,
  &:focus {
    color: $main-accent-color;
 }
  }


.modal--support .socials__icon {
      width: 40px;
      height: 40px;
  
  @media screen and (min-width: 1440px) {
        width: 48px;
        height: 48px;
  }
}